import React from 'react'
import marked from "marked";
import data from "../../api/data";
import Section from '../../components/Section';
import * as moment from 'moment';
import { markedInline } from '../../utils/helpers';
import CVTable from '../../components/CVTable';
import "./Talks.scss";

function formatDate(str, _default) {
  const date = moment(str, moment.ISO_8601, false);
  if (date.isValid()) {
    return date.format("MMM YYYY");
  }
  return _default;
}

function Item(talk) {
  const name = (<span className="talk-date">{formatDate(talk.date) || talk.date}</span>);
  const content = (<div>
    <div className="talk-title" dangerouslySetInnerHTML={{ __html: markedInline(talk.title) }} />
    <div>
      <span className="talk-venue">{talk.venue}{talk.location ? ', ' : '.'}</span>
      {talk.location && <span className="talk-location">{talk.location}.</span>}
    </div>
    
  </div>);
  return {name, content};
}

function Talks(props) {
  const {title, talks} = props;
  return (
    <Section id="talks">
      <h2 dangerouslySetInnerHTML={{__html: markedInline(title)}}></h2>
      {talks && (
        <CVTable className="talks" items={talks.map(Item)} />
      )}
        
    </Section>
  );
}


export default function DefaultTalks(props) {
  const talks = data.talks || [];
  return <Talks title='Invited Talks' talks={talks} {...props}/>;
}